body, html {
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  line-height: inherit;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
  font-family: sans-serif; }

#app {
  position: fixed;
  left: 0;
  right: 0;
  overflow: hidden;
  top: 0;
  bottom: 0;
  background-color: #131517;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  line-height: 40px;
  font-size: 20px; }

h1 {
  font-weight: 700;
  font-size: 84px;
  line-height: 80px;
  margin: 40px 40px 40px -0.085em; }

h2 {
  font-weight: 700;
  font-size: 77px;
  line-height: 80px;
  margin: 0 0 0 -0.085em; }

h3 {
  font-size: 51px;
  line-height: 60px;
  margin: 0 0 0 -0.085em; }

h4 {
  font-size: 35px;
  line-height: 60px;
  margin: 0 0 0 -0.085em; }

h5 {
  font-size: 20px;
  line-height: 26px;
  margin: 0 0 0 -0.085em; }

h6 {
  font-size: 15px;
  line-height: 30px;
  margin: 0 0 0 -0.085em; }

h3, h4, h5, h6 {
  margin: 0;
  font-weight: 600; }

a {
  color: inherit;
  text-decoration: none; }



